export default {
	data() {
		return {
			id: '',
			noticeInfo: {},
		};
	},
	onLoad(options) {
		this.id = options.id
		this.fetchNoticeDetail()
	},
	methods: {
		fetchNoticeDetail() {
			uni.request({
				url: `https://ciai.le-cx.com/index.php/api/notice/info?id=${this.id}`,
				success: res => {
					this.noticeInfo = res.data.data
				}
			})
		}
	}
}
